<template>
  <div class="hello">
    <div class="col-xl-12 col-12 content">
      <div class="col-lg-12 col-12">
        <div id="VR" ref="fullscreen">
          <embed ref="embedVR" src="https://chenchia-yu.github.io/a360/courtyard.html " type="">
          <div class="fullscreen" @click="go_fullscreen()">全螢幕
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import image from '../@/assets/'
import VuePageTitle from 'vue-page-title'
import Vue from 'vue'
Vue.use(VuePageTitle, {
  prefix: 'My App - ',
  suffix: '720'
})
export default {
  name: 'VR',
  title:"VR",
  components: {
    // hamburger
  },
  data () {
    return {

    }
  },
  mounted(){
    // this.go_fullscreen();
  },
  methods: {
    go_fullscreen(){
      this.$refs.fullscreen.requestFullscreen();
      this.$refs.embedVR.style.height = window.screen.height + "px";
      // this.Element.remove();
    }
  }

}

</script>

<style scoped>
  img.no_rotate{
    transform: rotate(0);
  }


</style>



